#fb-share-button {
  width: 21px;
  height: 22px;
  background: #3b5998;
  border-radius: 3px;
  font-weight: 600;
  padding: 5px;
  display: inline-block;
  position: static;
  margin-left: 12px;
}

#fb-share-button svg {
  width: 18px;
  fill: white;
  vertical-align: middle;
  border-radius: 2px
}

.share-btns {
  display: flex;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 100;
  margin-right: 12px;
  margin-bottom: 12px;
}

.share-btns__title {
  display: none;
  margin-right: 12px;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 0;
  margin-top: 8px;
  font-size: 12px;
}
