$square-small: 20px;
$square-big: 110px;
$square-medium: 60px;


.ten {
  margin-top: 40px;
  display: flex;
  width: 100%;
  justify-content: center;

  .one {
    margin-right: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .one-1 {
      width: $square-big*1.5;
      background-image: url("../img/ten/ten-start.svg");
      background-size: cover;
      align-self: flex-start;
      text-align: right;
    }
  }

  .brick {
    width: $square-big;
    height: $square-big;
    box-shadow: 1px 1px 0 0 white, 2px 2px 0 0 white, 3px 3px 0 0 white;
    transition: transform 300ms linear;

    &:hover {
      transform: scale(1.25);
    }
  }

  .one-4 {
    width: $square-big*2;
  }
}


@media (max-width: 800px) {
  .ten {
    .brick {
      width: $square-medium;
      height: $square-medium;
    }

    .one {
      margin-right: 20px;

      .one-1 {
        width: $square-medium*1.5;
        padding-left: 30px;
        box-sizing: border-box;
      }
    }

    .one-4 {
      width: $square-medium*2;
    }
  }
}

.ten--small {

  .one {
    margin-right: 20px;

    .one-1 {
      width: $square-small*1.5;
    }
  }

  .brick {
    width: $square-small;
    height: $square-small;
  }


  .one-4 {
    width: $square-small*2;
  }
}

.round-top-right {
  border-radius: 0 100% 0 0;
}

.round-top-left {
  border-radius: 100% 0 0 0;
}

.round-bottom-right {
  border-radius: 0 0 100% 0;
}

.round-bottom-left {
  border-radius: 0 0 0 100%;
}
