@import "ten.scss";
@import "car.scss";
@import "nav.scss";
@import "preload.scss";
@import "share-btns.scss";
@import "_cards/base.scss";

html {
  scroll-behavior: smooth;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Raleway', sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2 {
  margin: 0;
}

h1 {
  font-size: 58px;
  margin-bottom: 16px;
}


h2 {
  font-size: 48px;
  margin-bottom: 32px;
}

.main-text {
  font-size: 22px;
  line-height: 1.6;
  margin-bottom: 40px;
  margin-top: 0;
}

.text-bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.promo_text {
  margin: 20px auto;
  max-width: 800px;
  font-size: 20px;
  line-height: 1.6;
  text-align: center;
}

.promo {
  padding-bottom: 160px;
  color: white;
  background: linear-gradient(243.96deg, #05ccbb 10.6%, rgba(79, 154, 247, 0.961912) 49.07%, rgba(167, 89, 255, 0.926133) 85.89%);

  h1 {
    margin-top: 50px;
    margin-bottom: 50px;
    font-size: 74px;
    text-align: center;
  }

  .container {
    text-align: center;
  }

  .btn {
    margin-top: 20px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1190px;
  overflow: hidden;
}

.row {
  display: flex;
}

.bg-teal {
  background-color: #07c29e;
}

.bg-violet {
  background-color: #a353ff;
}

.bg-orange {
  background-color: #f59d20;
}

.history {
  position: relative;
  top: -100px;
  margin-bottom: -140px;
  padding: 150px 0;
  text-align: center;
  color: white;
  background-image: url("../img/bg/history-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

a.btn {
  display: inline-block;
  padding: 10px 20px;
  font-size: 24px;
  text-decoration: none;
  color: white;
  border: 2px solid white;
  border-radius: 8px;
  background-color: #a25bfc;
  box-shadow: 3px 3px 0 0 white, 5px 5px 0 0 white, 7px 7px 0 0 #139b80;
  transition: all 100ms linear;
  &:hover {
    transform: translateX(5px) translateY(5px);
    box-shadow: 1px 1px 0 0 white, 2px 2px 0 0 white, 3px 3px 0 0 #066754;
    background-color: #9247f1;
  }
}

.alumni {
  position: relative;
}

.interview {
  position: relative;
  top: -30px;
  margin-bottom: -30px;
  padding: 100px 0 200px;
  text-align: center;
  color: white;
  background-image: url('../img/bg/facts-bg.svg');
  background-repeat: no-repeat;
  background-size: cover;
}

.color-teal {
  color: #07c29e;
}

.social-media {
  text-align: center;
}

.practice {
  .card__title {
    text-align: left;
    font-size: 20px;
  }
  .card {
    font-size: 16px;
    box-shadow: 0 0 12px 0 rgba(black, 0.2);
  }
}

.content-img {
  margin-bottom: 40px;
}

.map {
  position: relative;
  top: -100px;
  margin-bottom: -100px;
  padding: 150px 0 100px;
  text-align: center;
  color: white;
  background-image: url('../img/bg/map-bg.svg');
  background-repeat: no-repeat;
  background-size: cover;
}

@media (max-width: 800px) {
  .promo h1 {
    font-size: 48px;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 30px;
  }
  .main-text {
    font-size: 18px;
  }
}

a.content-link {
  text-decoration: none;
  color: white;
  background-color: #a25bfc;
  padding: 2px;

  &:hover {
    background-color: #f59d20;
  }
}

.fotorama {
  margin-bottom: 40px;
}
