.preload {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #191d21;
  z-index: 1000;

  .container {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }

  .ten {
    .brick {
      animation: preload 1s 100ms linear;
      box-shadow: none;
      opacity: 0;

      &:hover {
        transform: none;
      }

      &.duration-1 {
        animation-duration: 10s;
        animation-delay: 200ms;
      }

      &.duration-2 {
        animation-duration: 10s;
        animation-delay: 400ms;
      }

      &.duration-3 {
        animation-duration: 10s;
        animation-delay: 600ms;
      }

      &.duration-4 {
        animation-duration: 10s;
        animation-delay: 800ms;
      }

      &.duration-5 {
        animation-duration: 10s;
        animation-delay: 1000ms;
      }
    }
  }
}


@keyframes preload {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
