.main-nav {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  a {
    color: #fff;
    text-decoration: none;
  }
}

.main-nav_menu {
  display: flex;
  list-style: none;

  .menu_item {
    padding: 5px 10px;

    &:hover {
      color: #a25bfc;
    }

    &.active {
      a {
        color: white;
      }

      background-color: #a25bfc;
      border-radius: 3px;

      &:hover {
        a {
          color: white;
        }

        background-color: #8b46e2;
      }
    }
  }
}

@media (max-width: 800px) {
  .main-nav_menu {
    display: none;
  }
}

.main-nav.main-nav--inner {
  margin-bottom: 80px;

  .menu_item {
    a {
      color: #1b1e21;

      &:hover {
        color: #a25bfc;
      }
    }

    &.active {
      a {
        color: white;
      }

      background-color: #a25bfc;
      border-radius: 3px;

      &:hover {
        a {
          color: white;
        }

        background-color: #8b46e2;
      }
    }
  }

}

.main-nav_logotype {
  flex-shrink: 0;
}
